@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* {
    margin:0;
    padding:0;
    box-sizing: border-box;
}

html {
    font-family: Arial, Helvetica, sans-serif;
}

div.app {
  box-sizing: border-box;
  margin: 0 auto;
  background-color: rgb(237, 240, 245);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.section_navigation {
  flex-basis: 15h;
}

header {
  flex-basis: 63vh;
}

main {
  background:#fefffb;
  flex-basis:80vh;
}
footer {
  flex-basis: 8vh;
}


::-webkit-scrollbar{
  width:10px;
}
::-webkit-scrollbar-track {
  background:rgb(218, 173, 26);
}
::-webkit-scrollbar-thumb {
  background: rgb(206, 201, 201);
  border-radius:25px;
}
::-webkit-scrollbar-thumb:hover {
  background-image: linear-gradient(#222) ;
}

header {
  position: relative;
  overflow: hidden;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  background-position: 80% 80%;
}

.home {
  font-family: "PT Sans", sans-serif;
  display: flex;
  background-color: white;
  margin: 0px 20px 80px 20px;
  padding-bottom: 10px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
}

.advices {
  background: rgb(255, 255, 253);
  border-radius: 2%;
  padding: 30px 15px 30px 30px;
  margin: 0 20px 0 20px;
  width: 300px;
  border: 3px black solid;
  transition: all 0.2s linear;
}
.advices:hover{
  transform:scale(0.95)
}

.advices__header {
  font-size: 20px;
  position: relative;
  padding-bottom: 10px;
}
.advices__header::after {
  content: "";
  display: inline-block;
  width: 2.6041666667vw;
  height: 0.2604166667vw;
  background: #ef002d;
  position: absolute;
  bottom: 0;
  left: 0;
}

.advices__author {
  color: black;
  font-style: italic;
  padding: 10px 0 10px 0;
}
.advices__paragraph {
}

@media (max-width:700px) {
