.offers {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
    font-size:30px;
    margin-bottom:40px;
  }
  
  .offers__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .offers__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .offers__items {
    margin-bottom: 24px;
  }
  
  .offers__item {
    display: flex;
    flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
    padding-bottom:15px;
  }
  
  .offers__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .offers__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  
  .offers__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .offers__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .offers__item__img:hover {
    transform: scale(1.1);
  }
  
  .offers__item__info {
    padding: 20px 30px 30px;
  }
  
  .offers__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  
  @media only screen and (max-width: 1024px) {
    .offers__item {
      margin-bottom: 2rem;
      display:flex;
      flex-direction: column;
    }
    .offers__wrapper {
      margin:0;
    }
  }


